import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';
import logo from './assets/logo.svg';

const getClientHeihgt = () => document.documentElement.clientHeight;

class Header extends Component {
  constructor (props) {
    super(props);
    this.state = {
      // [NOTE]: CSSの100vhでは、Safariのアドレスバーを含めて計算してしまうためJS制御
      height: getClientHeihgt()
    };

    window.addEventListener('resize', () => {
      this.setState({
        height: getClientHeihgt()
      });
    });
  }

  render () {
    return (
      <Router>
          <header id="header" className="masthead d-flex bg-dark" style={{
            height: `${this.state.height}px`
          }}>
          <div className="container text-center my-auto">
            <div className="row justify-content-center">
              <Link className="header-logo" to="#about">
                <img src={logo} alt="logo" />
              </Link>
            </div>
          </div>
          <div className="overlay"></div>
        </header>
      </Router>
    );
  }
}

export default Header;
