import React, { Component } from 'react';

class Footer extends Component {
  render () {
    const socialButton = (href, icon, addClass = '') => (
      <li className="list-inline-item">
        <a className={`social-link rounded-circle text-white bg-dark ${addClass}`} href={href} target="_blank" rel="noopener noreferrer">
          <i className={icon}></i>
        </a>
      </li>
    );

    return (
      <footer className="footer text-center">
        <div className="container">
          <ul className="list-inline mb-5">
            {socialButton('https://www.instagram.com/amp_yamachan/', 'icon-social-instagram', 'mr-3')}
            {socialButton('https://twitter.com/ampyamachan/', 'icon-social-twitter', 'ml-3')}
          </ul>
          <p className="text-muted small mb-0">Copyright &copy; ampyamachan.com 2019</p>
        </div>
      </footer>
    );
  };
}

export default Footer;
